import React, { Component } from 'react';
import {superRedbacket2x,superRedBacketText2x,menu2x,stockSrc,signInIocn} from '../public';
import RoomList from '../comment/roomlist/roomlist';
import Rank from '../comment/rank/rank';
import Menu from '../comment/menu/menu';
import Eoselem from '../comment/eosRecarge/eosRecarge';
import Invite from '../comment/invite/invite';
import Loading from '../comment/loading/loading';
import EOS from 'eosjs';
class App extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	addMenu:[],
    	addEos:[],
    	addInvite:[],
    	addLoading:[],
    	lang:"CN"
    };
    
  }
	menuCallBack(type){

		if('openEos'==type){

			this.setState({addMenu:[],addEos:[<Eoselem callBack={this.menuCallBack.bind(this)}></Eoselem>]});
		}else if('cancelEos'==type){
			this.setState({addEos:[]});

		}else if('openMenu'==type){

			this.setState({addMenu:[<Menu callBack={this.menuCallBack.bind(this)}></Menu>]});
		}else if('cancelMenu'==type){

			this.setState({addMenu:[]})
		}else if('openInvite'==type){
			this.setState({addInvite:[<Invite callBack={this.menuCallBack.bind(this)}></Invite>],addMenu:[]})
		}else if('cancelInvite'==type){
			this.setState({addInvite:[]})
		}else if('openLoading'==type){
			this.setState({addLoading:[<Loading></Loading>]});
		}else if('cancelLoading'==type){
			this.setState({addLoading:[]});
		}
	}
	openMenu(e){
		let ev=e||window.event;
		ev.stopPropagation();
		
		this.menuCallBack('openMenu');
		return false;
	}
	cancelMenu(){
		this.menuCallBack('cancelMenu');
	}
	goRoom(){

		window.open("room.html");
	}
	connect(){
//		let eos=EOS()
	}
	componentWillMount(){
		
		let _this=this;
//		_this.connect();
		_this.menuCallBack('openLoading');
		setTimeout(function(){
			_this.menuCallBack('cancelLoading');
		},1500);
	}
	
  render() {
  	let lang=localStorage.getItem("lang");//语种
  	lang=lang?lang:"CN";
  	let arr=[
  		<RoomList theme='1' key='1' callBack={this.goRoom.bind(this)}></RoomList>,
  		<RoomList theme='2' key='2' callBack={this.goRoom.bind(this)}></RoomList>,
  		<RoomList theme='3' key='3' callBack={this.goRoom.bind(this)}></RoomList>,
  		<RoomList theme='4' key='4' callBack={this.goRoom.bind(this)}></RoomList>
  	]
    return (
    	<div className="wrap" onClick={this.cancelMenu.bind(this)}>
	      <div className="app">
	        <div className="nav-wrap flex">
	        	<div>
	        		<img className="nav-rb" src={superRedbacket2x}/>
							<span className="nav-text">{lang=="CN"?"超級红包":"Super Hongbao"}</span>
	        	</div>
	        		<img className="nav-menu" src={menu2x} onClick={this.openMenu.bind(this)}/>
	        </div>
	        {arr}
	        <div className="flex fun-wrap">
	        	<li>
	        		<a href='' className="flex-start fun-1">
		        		<div className="img-box"><img src={signInIocn}/></div>
		        		<div>
		        			<p className="text-shadow">{lang=="CN"?"签到领红包":"Sign-in"}</p>
		        			<p>09:33:33</p>
		        		</div>
	        		</a>
	        	</li>
	        	<li>
	        		<a href='' className="flex fun-2">
		        		<div className="img-box">
		        			<img src={stockSrc}/>
		        			<span>{lang=="CN"?"成为股东":"Equity Swap"}</span>
		        		</div>
	        		</a>
	        	</li>
	        </div>
	      </div>
	      <Rank></Rank>
	      {this.state.addMenu}
	      {this.state.addEos}
	      {this.state.addInvite}
	      {this.state.addLoading}
      </div>
    );
  }
}

export default App;
