import React, { Component } from 'react';
import './rank.css';
import RankList from './ranklist';
class Rank extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	data:props.data?props.data:{}
    	,theme:props.theme?props.theme:1
    	
    };
    
  }
	render(){
		let lang=localStorage.getItem("lang");//语种
  		lang=lang?lang:"CN";
		let rankListArr=[
			<RankList theme='1' key='1'></RankList>,
			<RankList theme='2' key='2'></RankList>,
			<RankList theme='3' key='3'></RankList>,
			<RankList theme='4' key='4'></RankList>
		];
		return(
			<div className="rank-wrap">
				<h3 className="rank-title">{lang=="CN"?"排行榜":"Rank"}</h3>
				<div className="rank-th flex">
					<div>
						<span className="label label-default">{lang=="CN"?"排名":"Rank"}</span>
					</div>
					<div>
						<span className="label label-default">{lang=="CN"?"玩家":"Player"}</span>
					</div>
					<div>
						<span className="label label-default">{lang=="CN"?"红包奖金":"T.Reward(EOS)"}</span>
					</div>
					<div>
						<span className="label label-default">{lang=="CN"?"奖励":"Bonus(SUP)"}</span>
					</div>
				</div>
				{rankListArr}
			</div>
		)
	}
}

export default Rank;